import React, { Component } from 'react';
import './App.css';
import EventList from './EventList/EventList'
import CitySearch from './CitySearch/CitySearch'
import {useSelector, useDispatch} from 'react-redux'
import {increment, fetchEventsRequest, fetchEventsSuccess, fetchEventsFailure, getEvents} from './actions'

function App() {

  function enterMeetup() {
    dispatch(getEvents())
  }

  const counter = useSelector(state => state.counterReducer)
  const events = useSelector(state => state.updateEvents)
  const dispatch = useDispatch()

  const updateEvents = (lat, lon) => {
    return function(dispatch) {
      dispatch(fetchEventsRequest(lat, lon))
      .then(data => {
        const events = data
        dispatch(fetchEventsSuccess(events))
      })
      .catch(error => {
        dispatch(fetchEventsFailure(error))
      })
    }
  }

    return (
      <div className="App">
        <EventList events={events} />
        <CitySearch updateEvents={updateEvents} />
        <h1>Counter {counter}</h1>
        <div onClick={() => enterMeetup()}>Start using App!</div>
        <button onClick={() => dispatch(increment())}>+</button>
      </div>
    );
}

export default App;
